import React from "react";
import { useNavigate } from "react-router-dom";
import { Nav, NavLink, NavMenu } from "./NavbarElements";
    
const Navbar = () => {
	
  let navigate = useNavigate(); 
  const routeChange = () => { 
    let path = `/`; 
    navigate(path);
  } 	
	
  return (
    <>
      <Nav>
        <NavMenu>
        	 <img onClick={routeChange} class="logo" src="./logo512.png" alt="favicon" />
          <NavLink to="/adventures" activeStyle>
            ADVENTURES
          </NavLink>
          <NavLink to="/learning" activeStyle>
            LEARNING
          </NavLink>
          <NavLink to="/thoughts" activeStyle>
            THOUGHTS
          </NavLink>
          <NavLink to="/goals" activeStyle>
            GOALS
          </NavLink>
          <NavLink to="/places" activeStyle>
            PLACES
          </NavLink>
          <NavLink to="/cv" activeStyle>
            CV
          </NavLink>
        </NavMenu>
      </Nav>
    </>
  );
};
  
export default Navbar;
